<template>
  <div class="product">
    <div class="product__img">
      <img v-bind:src='recomended[getIdProd()].img' alt="" />
    </div>
    <h3>{{ recomended[getIdProd()].title }}</h3>
    {{ recomended[getIdProd()].description }}
    {{ recomended[getIdProd()].id }}
    {{ recomended[getIdProd()].price.old }}
    {{ recomended[getIdProd()].price.current }}
    <productSlider />
  </div>
</template>

<script>
import getData from './getData.vue'
import productSlider from './productSlider.vue'

export default {
  extends: getData,
  name: 'product',
  components: {
    productSlider
  },
  data () {
    return {
      recomended: this.recomended
    }
  },
  methods: {
    getIdProd: function () {
      return parseInt((this.$route.params.id) - 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.product {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  &__name {}
  &__img-wrapp {
    &-img {}
  }
}
</style>



